.sender__top,
.sender__options,
.sender,
.sender__option {
  display: flex;
  align-items: center;
}

.sender {
  margin-top: 30px;
  flex-direction: column;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
  width: 100%;
}

.sender__top {
  width: 90%;
  border-bottom: 1px solid #eff2f5;
  padding: 15px;
}

.sender__top > form {
  flex: 1;
  display: flex;
}

.sender__top > form > input {
  outline-width: 0;
  border: none;
  padding: 12px 20px;
  margin: 0 10px;
  border-radius: 999px;
  background-color: #eff2f5;
}

.sender__input {
  flex: 1;
}

.sender__top > form > button {
  display: none;
}

.sender__options {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  padding: 3px 15px;
  box-sizing: border-box;
}

.sender__option {
  flex: 1;
  padding: 10px;
  display: flex;
  justify-content: center;
  color: gray;
  margin: 5px;
}

.sender__option > .MuiSvgIcon-root {
  font-size: x-large;
}

.sender__option > h3 {
  font-size: medium;
  margin-left: 10px;
}

.sender__option:hover {
  background-color: #eff2f5;
  border-radius: 20px;
  cursor: pointer;
}
